                />
                <path
                  d="M586.173,1232.221c-0.292,1.522-1.38,2.303-2.682,2.885l-0.102,0.074c0.784-1.14,0.882-2.682,2.09-3.574   C585.71,1231.811,585.942,1232.016,586.173,1232.221z"
